import Vue from 'vue'
import App from './App'
import router from './router'
import {currency } from './util/currency'

Vue.config.productionTip = false
Vue.filter("currency", currency)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



